import React, {useEffect, useState} from 'react';
import './App.css';
import {
  create, get,
} from "@github/webauthn-json/browser-ponyfill";
import {stringify} from "querystring";

//        
const testUser = {
  login: "test1",
  password: "test1"
}

type passkeyType = {
  username: string;
  publicKey: string
}

function App() {
  //  
  const [isAuth, setIsAuth] = useState(false);
  // ,     passkey
  const [isAuthPasskey, setIsAuthPassKey] = useState(false);
  //   
  const [errorAuth, setErrorAuth] = useState("");
  const [currentUser, setCurrentUser] = useState<string | null>(null);
  const [passkeysArr, setPasskeysArr] = useState<passkeyType[]>([]);
  const [result, setResult] = useState<string | undefined>()
  const [resultAuthn, setResultAuthn] = useState<string | undefined>()

  //     base64
  function b64EncodeUnicode(str: string) {
    // first we use encodeURIComponent to get percent-encoded UTF-8,
    // then we convert the percent encodings into raw bytes which
    // can be fed into btoa.
    return btoa(encodeURIComponent(str).replace(/%([0-9A-F]{2})/g,
      function toSolidBytes(match, p1) {
        return String.fromCharCode(Number('0x' + p1));
      }));
  }

  const createNewPasskey = (userName: string) => {
    //   userIdBase64       
    let userIdBase64 = b64EncodeUnicode(userName);
    const createOptions: CredentialCreationOptions = {
      // ,      WebAuthn
      publicKey: {
        // ,   ,    
        rp: {
          name: "Passkeys test page",
          id: "alexanderpm.github.io",
        },
        // ,    ,     
        user: {
          // `userIdBase64` is the user's passkey ID, from the database,
          // base64-encoded.
          id: Uint8Array.from(atob(userIdBase64), c => c.charCodeAt(0)),
          // `username` is the user's username. Whatever they would type
          // when signing in with a password.
          name: userName,
          // `displayName` can be a more human name for the user, or
          // just leave it blank.
          displayName: userName,
        },

        // This lists the ids of the user's existing credentials. I.e.
        //   SELECT id FROM passkeys WHERE username = ?
        // and supply the resulting list of values, base64-encoded, as
        // existingCredentialIdsBase64 here.
        excludeCredentials: passkeysArr.map((item) => {
          return {
            type: "public-key",
            id: Uint8Array.from(atob(item.publicKey), c => c.charCodeAt(0)),
            transports: ['internal'],
          };
        }),

        // Boilerplate that advertises support for P-256 ECDSA and RSA
        // PKCS#1v1.5. Supporting these key types results in universal
        // coverage so far.
        pubKeyCredParams: [{
          type: "public-key",
          alg: -7
        }, {
          type: "public-key",
          alg: -257
        }],

        // Unused during registrations, except in some enterprise
        // deployments. But don't do this during sign-in!
        challenge: new Uint8Array([0]),

        authenticatorSelection: {
          authenticatorAttachment: "platform",
          requireResidentKey: true,
          residentKey: "required",
          userVerification: "required"
        },

        //          
        timeout: 18000,
      }
    };

    create(createOptions).then(
      (pubKeyCredential) => {
        const response = pubKeyCredential;
        if (response?.id) {
          setIsAuth(true)
          setPasskeysArr(oldArray => [...oldArray, {username: userName, publicKey: response.id}]);
          setResult(JSON.stringify(response.toJSON()));
          const sha256 = require('js-sha256');
          const hashed = sha256(response.toJSON().response.clientDataJSON);
          alert("  + /// " + hashed);
        }

      },
      (response) => handleCreationError(response));
  }

  function handleCreationError(e: Error) {
    if (e instanceof DOMException) {
      switch (e.name) {
        case 'InvalidStateError':
          console.log('InvalidStateError');
          return;

        case 'NotAllowedError':
          console.log('NotAllowedError');
          return;
      }
    }

    console.log(e);
  }

  const isPublicKeyForCurrentUser = (userName: string | null) => {
    if (passkeysArr.length < 1 || !userName) {
      return false;
    }
    let result = false;
    passkeysArr.forEach((passkeys) => {
      if (passkeys.username === userName) {
        result = true;
      }
    })
    return result;
  }


  const handlerOnClick = () => {
    if (currentUser) {
      const publicKey = isPublicKeyForCurrentUser(currentUser);
      if (!publicKey) {
        createNewPasskey(currentUser);
      }
      if (publicKey) {
        // To abort a WebAuthn call, instantiate an `AbortController`.
        const abortController = new AbortController();

        const publicKeyCredentialRequestOptions: CredentialRequestOptions = {
/*          mediation: "conditional" as CredentialMediationRequirement,*/
          publicKey: {
            // Server generated challenge
            challenge: Uint8Array.from(atob(b64EncodeUnicode(currentUser)), c =>
              c.charCodeAt(0)),
            // The same RP ID as used during registration
            rpId: "alexanderpm.github.io",
          },
          signal: abortController.signal,
        };
        get(publicKeyCredentialRequestOptions).then(
          (response) => {
            setResultAuthn(JSON.stringify(response))
            if (response?.id) {
              setIsAuth(true)
            }
          }, () => alert("error")
        );
      }
    }
  }
  // Logout
  const handlerLogout = () => {
    setIsAuth(false);
  }
  //     
  const handlerPasswordAuth = (event: any) => {
    //       
    event.preventDefault();
    const login = event.target.elements.login.value;
    const password = event.target.elements.password.value;
    if (testUser.login === login && testUser.password === password) {
      setIsAuth(true);
      setErrorAuth("");
      setCurrentUser(login);
    }
    else {
      setIsAuth(false);
      setErrorAuth("   ")
    }
  }

  useEffect(() => {
    //             
    if (!window.PublicKeyCredential ||
      !(PublicKeyCredential as any).isConditionalMediationAvailable) {
      setIsAuthPassKey(false);
      return;
    }
    Promise.all([
      (PublicKeyCredential as any).isConditionalMediationAvailable(),
      //  ,  Promise.
      //   true   ,   ,   .
      PublicKeyCredential
        .isUserVerifyingPlatformAuthenticatorAvailable()])
      .then((values) => {
        if (values.every(x => x === true)) {
          setIsAuthPassKey(true);
        }
      })
  }, [])


  return (
    <div className="App" style={{display: "flex", flexDirection: "column", margin: "10px"}}>
      <h2>Testing password and passkey authorization</h2>
      {/*      */}
      {((!isAuth && !isAuthPasskey) || (!isAuth && isAuthPasskey)) && (
        <form onSubmit={handlerPasswordAuth} style={{display: "flex", flexDirection: "column", margin: "10px"}}>
          <input
            type="text"
            name="login"
            placeholder="login"
            style={{margin: "10px auto"}}
          />
          <input
            type="text"
            name="password"
            placeholder="password"
            style={{margin: "10px auto"}}
          />
          {errorAuth && (
            <p style={{color: "red"}}>{errorAuth}</p>
          )}
          <button
            style={{ width: '120px', height: '30px', margin: "10px auto"}}
          >
            
          </button>
        </form>
      )}
      {!isAuth && isAuthPasskey && (
        <div style={{display: "flex", flexDirection: "column", margin: "10px"}}>
          <p>
            A passkey
          </p>
          <input
            type="text"
            name="username"
            placeholder="email"
            autoComplete="username webauthn"
            style={{margin: "10px auto"}}
          />
          <button
            onClick={handlerOnClick}
            style={{ width: '150px', height: '50px', margin: "10px auto"}}
          >
            
          </button>
        </div>
      )}
      {/*   */}
      {isAuth && (
        <>
          <p>  !</p>
          {isAuthPasskey && !isPublicKeyForCurrentUser(currentUser) && (
            <div  style={{display: "flex", flexDirection: "column", margin: "10px"}}>
              <p>
                    passkey!    passkey   
                   passkey!
              </p>
              <button
                onClick={handlerOnClick}
                style={{ width: '120px', height: '40px', margin: "10px auto"}}
              >
                 passkey
              </button>
            </div>
          )}
          <button
            onClick={handlerLogout}
            style={{ width: '120px', height: '30px', margin: "50px auto"}}
          >
            
          </button>
        </>
      )}
      {result && (
        <p style={{wordBreak: "break-word"}}>{result}</p>
      )}
      {resultAuthn && (
        <p style={{wordBreak: "break-word", color: "red" }}>{resultAuthn}</p>
      )}
    </div>
  );
}

export default App;
